">\n                                    <div class=\"row m-auto justify-content-center align-items-center\" style=\"width: 100%; height: 100%;\">\n                                        <div class=\"col-12 col-md-12 col-sm-12 p-0\">\n                                            <div class=\"row align-items-center justify-content-center mx-auto mb-3 travel-card-title-row\" style=\"height:100px;width:100%;\">\n                                                <div class=\"col-md-10 p-0\">\n                                                    <div class=\"row mr-auto ml-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:250px;\">\n                                                        <h1 class=\"title-1\"><span> З</span>абыли</h1>\n                                                        <img src=\"/images/blue-brush.svg\" class=\"blue-brush\" style=\"position: absolute;\"/>\n                                                    </div>\n                                                </div>\n                                                <div class=\"col-md-7 p-0\">\n                                                    <div class=\"row ml-auto mr-0 my-auto travel-card-title\" style=\"position:relative;height:50px;width:280px;\">\n                                                        <h1 class=\"title-2\"><span>свой пароль ? </span></h1>\n                                                        <img src=\"/images/orange-brush.svg\" class=\"orange-brush\" style=\"position: absolute;\"/>\n                                                    </div>\n                                                </div>\n                                            </div>\n                                        </div>\n                                    </div>\n                                    <div class=\"form-signin\">\n                                        <p style=\"font-family: 'Open Sans';font-weight: 700; font-style: italic; text-align: center; color:#0d274b; font-size: 12px\">Введите Ваш email. На него придёт письмо с ссылкой, перейдя по которой вы сможете поменять пароль </p>\n                                        <ValidationObserver v-slot=\"{ invalid }\">\n                                            <form autocomplete=\"off\" @submit.prevent=\"requestResetPassword\" method=\"post\">\n                                                <div class=\"form-group\">\n                                                    <div class=\"form-label-group\">\n                                                        <h4 class=\"multiselect-title\">Email</h4>\n                                                        <ValidationProvider name=\"email\" rules=\"required\" v-slot=\"{ errors }\" style=\"width: 100%;text-align: center; margin-bottom: 17px\">\n                                                            <div class=\"row multiselect__tags align-items-center justify-content-center m-auto\" style=\"width: 100%;height: 100%;\">\n                                                                <input type=\"text\" class=\"multiselect__input travel-text-control form-control\" v-model=\"email\" placeholder=\"Email\">\n                                                            </div>\n                                                            <span class=\"validate-error\">{{ errors[0] }}</span>\n                                                        </ValidationProvider>\n                                                    </div>\n                                                </div>\n                                                <button type=\"submit\" class=\"btn btn-lg btn-travel btn-block text-uppercase mt-3\" :disabled=\"invalid||loading\">\n                                                    <div class=\"row align-items-center justify-content-center m-auto w-100 h-100 text-center\">\n                                                        <span v-if=\"loading\" class=\"spinner-border spinner-border-sm mx-2\" role=\"status\" aria-hidden=\"true\"></span>\n                                                       Отправить\n                                                    </div>\n                                                </button>\n                                            </form>\n                                        </ValidationObserver>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n<!--    <div class=\"container\">-->\n<!--    <div class=\"row justify-content-center\">-->\n<!--        <div class=\"col-6\">-->\n<!--            <div class=\"card card-default\">-->\n<!--                <div class=\"card-header\">Reset Password</div>-->\n<!--                <div class=\"card-body\">-->\n<!--                    <form autocomplete=\"off\" @submit.prevent=\"requestResetPassword\" method=\"post\">-->\n<!--                        <div class=\"form-group\">-->\n<!--                            <label for=\"email\">E-mail</label>-->\n<!--                            <input type=\"email\" id=\"email\" class=\"form-control\" placeholder=\"user@example.com\" v-model=\"email\" required>-->\n<!--                        </div>-->\n<!--                        <button type=\"submit\" class=\"btn btn-primary\">Send Password Reset Link</button>-->\n<!--                    </form>-->\n<!--                </div>-->\n<!--            </div>-->\n<!--        </div>-->\n<!--    </div>-->\n<!--</div>-->\n</template>\n\n<script>\n    export default {\n        data() {\n            return {\n                email: null,\n                has_error: false,\n                loading: false\n            }\n        },\n        methods: {\n            requestResetPassword() {\n                this.loading = true;\n                this.$http.post(\"/auth/reset-password\", {email: this.email}).then(result => {\n                    this.response = result.data;\n                    this.loading=false;\n                    console.log(result.data);\n                    this.sendMessage(result.data.message)\n                }, error => {\n                    this.loading=false;\n                    console.error(error);\n                });\n            },\n            sendMessage(message) {\n                this.$notify({\n                    group: 'info',\n                    type: 'travel',\n                    title: 'Сообщение от TravelClub',\n                    text: message\n                });\n            },\n        }\n    }\n</script>\n<style scoped>\n    .btn-visibility{\n        position: absolute;\n        background: transparent;\n        border: none;\n        outline: none!important;\n        color: #063065;\n        font-size: 20px;\n    }\n    .card-signin {\n        border: 0;\n        border-radius: 1rem;\n        box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.1);\n    }\n\n    .card-signin .card-title {\n        margin-bottom: 2rem;\n        font-weight: 300;\n        font-size: 1.5rem;\n    }\n\n    .card-signin .card-body {\n        padding: 2rem;\n    }\n\n    .form-signin {\n        width: 100%;\n    }\n\n    .form-signin .btn {\n        font-size: 80%;\n        border-radius: 5rem;\n        letter-spacing: .1rem;\n        font-weight: bold;\n        padding: 1rem;\n        transition: all 0.2s;\n    }\n\n    .form-label-group {\n        position: relative;\n        margin-bottom: 1rem;\n    }\n\n    .form-label-group input {\n        height: auto;\n        border-radius: 2rem;\n    }\n\n    .form-label-group>input,\n    .form-label-group>label {\n        padding: var(--input-padding-y) var(--input-padding-x);\n    }\n\n    .form-label-group>label {\n        position: absolute;\n        top: 0;\n        left: 0;\n        display: block;\n        width: 100%;\n        margin-bottom: 0;\n        /* Override default `<label>` margin */\n        line-height: 1.5;\n        color: #495057;\n        border: 1px solid transparent;\n        border-radius: .25rem;\n        transition: all .1s ease-in-out;\n    }\n\n    .form-label-group input::-webkit-input-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input:-ms-input-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input::-ms-input-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input::-moz-placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input::placeholder {\n        color: transparent;\n    }\n\n    .form-label-group input:not(:placeholder-shown) {\n        padding-top: calc(var(--input-padding-y) + var(--input-padding-y) * (2 / 3));\n        padding-bottom: calc(var(--input-padding-y) / 3);\n    }\n\n    .form-label-group input:not(:placeholder-shown)~label {\n        padding-top: calc(var(--input-padding-y) / 3);\n        padding-bottom: calc(var(--input-padding-y) / 3);\n        font-size: 12px;\n        color: #777;\n    }\n\n\n    /* Fallback for Edge\n    -------------------------------------------------- */\n\n    @supports (-ms-ime-align: auto) {\n        .form-label-group>label {\n            display: none;\n        }\n        .form-label-group input::-ms-input-placeholder {\n            color: #777;\n        }\n    }\n\n    /* Fallback for IE\n    -------------------------------------------------- */\n\n    @media all and (-ms-high-contrast: none),\n    (-ms-high-contrast: active) {\n        .form-label-group>label {\n            display: none;\n        }\n        .form-label-group input:-ms-input-placeholder {\n            color: #777;\n        }\n    }\n\n    .title-1 {\n        position: absolute;\n        z-index: 1;\n        left: 50px;\n        top: 10px;\n        color:#062348;\n        font-family: 'Bello Pro' !important;\n        font-size: 30px;\n    }\n    .title-1 span {\n        font-size: 40px;\n    }\n    .title-2 {\n        position: absolute;\n        z-index: 1;\n        left: 70px;\n        top: 22px;\n        color: white;\n        font-family: 'Open Sans' !important;\n        font-weight: 900;\n        font-size: 20px;\n    }\n    .title-2 span {\n        text-transform: uppercase;\n    }\n    .orange-brush {\n        top: 2px;\n        left: 10px;\n        width: 283px;\n        height: 60px;\n    }\n    .blue-brush{\n        top: 10px;\n        left: -55px;\n        width: 330px;\n        height: 50px;\n        opacity: 1;\n    }\n    .multiselect-title {\n        color: #062348 !important;\n        font-family: \"Bello Pro\";\n        /*font-weight: 700;*/\n    }\n    .multiselect__tags {\n        border: 1px solid #f08b23;\n        border-radius: 20px;\n        font-size: 1rem;\n        min-height: 40px;\n    }\n    .multiselect__tags .travel-text-control{\n        color: #062348;\n        font-family: \"Open Sans\" !important;\n        font-weight: 900 !important;\n    }\n\n    .multiselect__tags .travel-text-control:focus{\n        outline: none!important;\n        box-shadow: none !important;\n    }\n    .validate-error{\n        display: block !important;\n        height: 17px;\n    }\n    .btn {\n        background: #0f213d;\n        color: white;\n        text-transform: uppercase;\n        font-size: 24px;\n        font-family: \"Open Sans\";\n        font-weight: 900;\n        border: 0;\n        border-radius: 50px;\n        outline: none;\n    }\n    .btn:focus {\n        outline: none !important;\n        box-shadow: none !important;\n    }\n    .btn-travel {\n        border-radius: 50px;\n        background: #f08b23;\n        color: white;\n        text-transform: uppercase;\n        font-family: \"Roboto\", sans-serif;\n        font-weight: 900;\n        font-size: 20px;\n    }\n</style>\n"],"sourceRoot":""}]);
